body {
  background: #FFF;
  line-height: 1.7;
  font-size: $fz;
  @include reg;
  color: $black;
  min-width: 320px;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm;
    line-height: 1.5; } }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

@mixin scrollbar($background: $black) {

  &::-webkit-scrollbar {
    width: 6px; }
  &::-webkit-scrollbar-track {
    background-color: $gray-dark;
    box-shadow: inset 2px 0 0px $background, inset -2px 0 0px $background;
    outline: none; }
  &::-webkit-scrollbar-thumb {
    background-color: $gray-dark;
    @include tr(background-color .3s);
    @include hover {
      background-color: $gray-light; } } }


.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.animated-img {
  &[data-loaded="true"] {
    animation: fadeIn 1s; } }

.overflow-hidden {
  overflow: hidden; }
.overflow-visible {
  overflow: visible !important; }

.out {
  &.has-fixed-header {
    padding-top: $header-height;
    @include sm {
      padding-top: $header-height-sm; } } }

.container {
  display: block;
  max-width: 1440px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  // +xlmin
 }  //   max-width: 1440px

.logo {
  display: block;
  font-size: 0;
  .icon {
    @include css-lock(21, 33, 320, 575, font-size); } }

.scroll-to {
  width: 90px;
  height: 90px;
  background-color: $yellow;
  position: relative;
  color: $white;
  @include flex(center, center);
  flex: 0 0 90px;
  @include sm {
    width: 70px;
    height: 70px;
    flex: 0 0 70px; }
  .icon {
    font-size: $fz-arrow;
    @include tr(transform .5s);
    @include sm {
      font-size: $fz-arrow-sm; } }
  &--down {
    margin-top: 20px;
    .icon {
      transform: translate(0, -100%) rotate(90deg); }
    @include sm {
      margin-top: 15px; }
    @include hover {
      .icon {
        transform: translate(0, 0) rotate(90deg); } } }
  &--up {
    align-items: flex-end;
    .icon {
      transform: rotate(-90deg); }
    @include hover {
      .icon {
        transform: translate(0, -50%) rotate(-90deg); } } } }

.word {
  $this: &;
  // +black
  @include bold;
  @include css-lock(70, 236, 320, 1499, font-size);
  line-height: .9;
  letter-spacing: -.05em;
  text-transform: uppercase;
  position: absolute;
  z-index: 0;
  opacity: 0;
  &__inner {
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    @include image('black-pattern.png');
    background-repeat: repeat;
    background-size: auto;
    color: $gray-lighten; }

  &--sm {
    @include css-lock(70, 170, 320, 1499, font-size); }
  &--purple {
    // opacity: 1
    color: $purple-darken;
    #{$this}__inner {
      background-image: none;
      -webkit-text-fill-color: $purple-darken;
      -webkit-background-clip: initial; } } }




.link-arrow {
  $this: &;
  @include bold;
  text-transform: uppercase;
  @include hover {
    .icon {
      transform: translate(15px, 0); } }
  .icon {
    font-size: 8px;
    margin-left: 15px;
    fill: $gray-light;
    @include tr(transform .3s);
    @include sm {
      margin-left: 5px; } }
  &--prev {
    .icon {
      transform: scaleX(-1);
      margin-left: 0;
      margin-right: 15px;
      @include sm {
        margin-right: 5px; } }
    @include hover {
      .icon {
        transform: translate(-15px, 0) scaleX(-1); } } }
  &--short {
    .icon {
      margin-left: 0; }
    #{$this}__arrow {
      display: block; } } }


.scrolled-text {
  font-size: 10px;
  max-height: 460px;
  overflow: auto;
  position: relative;
  // padding-right: 8px
  @include scrollbar;
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  h1 {
    @extend .h6; }
  h2 {
    @extend .h6;
    @extend .h6--ttn;
    @extend .h6--lg; }
  p {
    margin-bottom: 20px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } } }

.overlayed {
  position: relative;
  overflow: hidden;
  @include before {
    @include coverdiv;
    z-index: 2;
    background-color: $yellow;
    transform: translate(-100%, 0); } }

.recommend {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  &__article {
    width: 33.33333%;
    padding-left: 15px;
    padding-right: 15px;
    @include md {
      width: 100%; } } }

.terms {
  max-width: 930px;
  margin-left: auto;
  margin-right: auto;
  article {
    margin-bottom: 80px;
    @include lg {
      margin-bottom: 60px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 30px; }
    @include xs {
      margin-bottom: 20px; }
    &:first-child {
      margin-bottom: 100px;
      @include xl {
        margin-bottom: 80px; }
      @include lg {
        margin-bottom: 60px; }
      @include md {
        margin-bottom: 40px; }
      @include sm {
        margin-bottom: 30px; }
      @include xs {
        margin-bottom: 20px; } } }
  h1 {
    @extend .title;
    text-align: center;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  h2 {
    text-align: center;
    @extend .h4;
    @include bold;
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  blockquote {
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; }
    li {
      margin-bottom: 0; } }
  p, li, ul, ol, article, blockquote {
    &:last-child {
      margin-bottom: 0; } }
  p, li, dd, ol, ul, dl {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  ul {
    li {
      &::before {
        content: "\2022";
        // width: 1em
        display: inline-block;
        margin-right: .5em; } } }
  ol {
    list-style-type: decimal;
    padding-left: 1em; }
  dl {
    @include clr; }
  dt {
    float: left;
    margin-right: .2em; }
  a {
    color: $blue;
    text-decoration: underline; } }

.author {
  padding-top: 10px;
  border-top: 1px solid  $yellow;
  &__name {
    @include bold; }
  &__text {
    font-size: 10px;
    color: $gray-light; } }

.laptop {
  position: relative;
  text-align: center;
  img {
    display: inline-block; }
  &__inner {
    display: inline-block;
    position: relative; }
  &__bg {
    @include cover;
    background-size: contain;
 }    // padding-top: 60%
  &__img {
    position: absolute;
    z-index: 1;
    left: 14%;
    right: 13%;
    top: 6%;
    bottom: 9%;
    @include cover;
    overflow: hidden; } }

.underlined {
  position: relative;
  @include before {
    position: absolute;
    z-index: 0;
    width: 100%;
    left: 0;
    bottom: .1em;
    background-color: $yellow;
    height: 6px;
    @include md {
      height: 3px; } }
  span {
    position: relative;
    z-index: 1; }
  &--sm {
    @include before {
      height: 4px;
      bottom: .3em; } }

  &--hover {
    overflow: hidden;
    display: inline-block;
    @include before {
      transform: translate(-100%, 0);
      @include tr(transform .3s); }
    @include hover {
      @include before {
        transform: translate(0, 0); } } } }
