strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal;
  @include italic; }

blockquote {
  @include italic;
  background-color: $gray-lighten;
  padding: 25px;
  border-left: 4px solid $yellow;
  @include md {
    padding: 15px;
    border-left-width: 2px; }
  @include sm {
    padding: 10px; }
  li {
    margin-bottom: 0; } }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h2, h2 {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h3, h3 {
  @include css-lock(21, 36, 320, 1499, font-size);
  @include light;
  text-transform: uppercase;
  line-height: 1.2;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h4, h4 {
  @include css-lock(18, 24, 320, 1499, font-size);
  text-transform: uppercase;
  line-height: 1.2;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--medium {
    @include medium; }
  &--bold {
    @include bold; } }

.h5, h5 {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h6, h6 {
  text-transform: uppercase;
  font-size: $fz;
  @include bold;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--lg {
    font-size: 16px; } }

.title {
  display: block;
  text-transform: uppercase;
  @include bold;
  @include css-lock(28, 48, 320, 1499, font-size);
  line-height: 1;
  @include tr(color .3s);
  small {
    font-size: .75em;
    @include light; }

  &--sm {
    @include css-lock(21, 36, 320, 1499, font-size); }
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--medium {
    @include medium; }
  &--light {
    @include light; } }

.text {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--light {
    @include light; }
  &--gray-light {
    color: $gray-light; }
  &--reg {
    @include reg; } }
