.pi-section {
  position: relative;
  overflow: hidden;
  @include css-lock(30, 100, 320, 1499, padding-top);
  @include css-lock(30, 100, 320, 1499, padding-bottom);
  > .container {
    position: relative;
    z-index: 1; }
  &__word {
    right: -5%;
    top: 0;
    white-space: nowrap; }
  &__title {
    text-align: center;
    max-width: 880px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
    @include sm {
      margin-bottom: 20px; }
    @include xs {
      margin-bottom: 10px; } }
  &__title-lg {
    max-width: 1000px; }
  &__title-mb-lg {
    @include css-lock(30, 100, 320, 1499, margin-bottom); }
  &__subttl {
    max-width: 510px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 60px;
    text-align: center;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; }
    @include xs {
      margin-bottom: 10px; } }
  &__right-img {
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: 0;
    text-align: right;
    img {
      display: inline-block; }
    @include xl {
      img {
        max-width: 80%; } }
    @include lg {
      img {
        max-width: 70%; } }
    @include md {
      img {
        max-width: 50%; } }
    @include sm {
      display: none; } }
  &__links {
    @include flex(center, center);
    padding-top: 60px;
    padding-bottom: 60px;
    .link-arrow {
      margin-left: 20px;
      margin-right: 20px; }
    @include lg {
      padding-top: 40px;
      padding-bottom: 40px; }
    @include sm {
      padding-top: 20px;
      padding-bottom: 20px;
      .link-arrow {
        margin-left: 5px;
        margin-right: 5px; } }
    @include xs {
      flex-direction: column;
      padding-bottom: 0;
      .link-arrow {
        margin-bottom: 10px; } } }
  &__img-center {
    text-align: center;
    img {
      display: inline-block; } } }

