.nav {
  $this: &;
  @include light;
  li {
    display: inline-block;
    position: relative;
    @include notlast {
      margin-right: 45px; }
    &.has-dropdown {
      a {
        position: relative;
        padding-right: 15px;
        @include before {
          @include icon-arrow(8, 8, 1, currentColor, -45);
          position: absolute;
          top: .4em;
          right: 0; } }
      &:hover {
        #{$this}--dropdown {
          visibility: visible;
          opacity: 1; } } } }
  a {
    @include tr(color .3s);
    text-transform: uppercase;
    display: inline-block;
    @include hover {
      color: $gray; } }

  &--dropdown {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: 1;
    top: calc(100%);
    padding-top: 25px;
    left: 0;
    color: $gray;
    white-space: nowrap;
    @include tr(opacity .3s, visibility .3s);
    li {
      display: block;
      @include notlast {
        margin-bottom: 15px; } }
    a {
      text-transform: none;
      @include before {
        display: none; }
      @include hover {
        color: $white; } } }

  &--lg {
    // color: $white
    // font-size: 24px
    @include css-lock(16, 24, 320, 1499, font-size);
    text-transform: uppercase;
    @include bold;
    li {
      display: block;
      position: relative;
      padding-left: 45px;
      @include notlast {
        margin-right: 0;
        margin-bottom: 65px;
        @include lg {
          margin-bottom: 45px; }
        @include md {
          margin-bottom: 35px; }
        @include sm {
          margin-bottom: 25px; }
        @include xs {
          margin-bottom: 15px; }
        @include xss {
          margin-bottom: 5px; } }
      @include lg {
        padding-left: 35px; }
      @include md {
        padding-left: 25px; }
      @include sm {
        padding-left: 20px; }
      @include xss {
        padding-left: 0; }
      span {
        font-size: .58em;
        @include reg;
        position: absolute;
        left: 0;
        top: .5em;
        @include rotate(-90);
        @include xss {
          display: none; } } }
    a {
      position: relative;
      overflow: hidden;
      @include hover {
        color: inherit;
        @include before {
          transform: translate(0, 0); } }
      @include before {
        height: 6px;
        left: -5px;
        right: -5px;
        position: absolute;
        z-index: -1;
        background-color: $yellow;
        bottom: .3em;
        transform: translate(-100%, 0);
        @include tr(transform .3s); } } }

  &--sm {
    // color: $white
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 25px;
        @include xs {
          margin-bottom: 15px; }
        @include xss {
          margin-bottom: 5px; } } }
    a {
      text-transform: none;
      overflow: hidden;
      position: relative;
      @include hover {
        color: inherit;
        @include before {
            transform: translate(0, 0); } }
      @include before {
        height: 4px;
        left: -5px;
        right: -5px;
        position: absolute;
        z-index: -1;
        background-color: $yellow;
        bottom: .3em;
        transform: translate(-100%, 0);
        @include tr(transform .3s); } } }

  &--articles {
    @include reg;
    li {
      // margin-left: 35px
      // margin-right: 35px
      @include css-lock(5, 35, 320, 1499, margin-left);
      @include css-lock(5, 35, 320, 1499, margin-right);
      @include notlast {
        @include css-lock(5, 35, 320, 1499, margin-right); } }

    a {
      position: relative;
      overflow: hidden;
      sup {
        display: inline-block;
        vertical-align: top;
        font-size: .7em; }
      @include hover {
        color: inherit;
        @include before {
            transform: translate(0, 0); } }
      @include before {
        height: 4px;
        left: -5px;
        right: -5px;
        position: absolute;
        z-index: -1;
        background-color: $yellow;
        bottom: .3em;
        transform: translate(-100%, 0);
        @include tr(transform .3s); } } } }

