.numbers {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px; }

.number {
  opacity: 0;
  width: 25%;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
  @include notlast {
    border-right: 1px dashed  $c-border; }
  @include md {
    width: 50%;
    margin-bottom: 15px;
    &:nth-child(2n -2) {
      border-right: 0; } }
  @include xss {
    width: 100%;
    @include notlast {
      border-right: 0; } }

  &__title {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    @include xl {
      br {
        display: none; } } } }
