.principles {
  $this: &;
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;

  &--circle {
    margin-left: 0;
    margin-right: 0;
    // justify-content: space-between
    display: block;
    position: relative;
    @include clr;
    .principle {
      &__wrap {
        padding-left: 0;
        padding-right: 0;
        width: 50%;
        max-width: 370px;
        margin-bottom: 100px;
        float: left;
        clear: left;
        @include xl {
          margin-bottom: 80px; }
        @include lg {
          margin-bottom: 70px; }
        @include md {
          margin-bottom: 50px; }
        @include sm {
          margin-bottom: 30px; }
        @include xs {
          width: auto;
          max-width: none;
          float: none !important;
          margin-bottom: 15px; }
        &:nth-child(even) {
          float: right;
          clear: right; }
        &:nth-child(1) {
          margin-left: percentage(1/12);
          @include md {
            margin-left: 0; } }
        &:nth-child(2) {
          margin-right: percentage(1/12);
          @include md {
            margin-right: 0; } }
        &:nth-child(5) {
          margin-left: percentage(1/12);
          @include md {
            margin-left: 0; } }
        &:nth-child(6) {
          margin-right: percentage(1/12);
          @include md {
            margin-right: 0; } } } }
    #{$this}__img {
      @include vcenter;
      z-index: 0;
      text-align: center;
      img {
        display: inline-block; }
      @include xl {
        img {
          max-width: 80%; } }
      @include lg {
        img {
          max-width: 60%; } }
      @include md {
        display: none; } } }

  &--two-col {
    position: relative;
    @include before {
      position: absolute;
      height: 1px;
      background-color: $white;
      bottom: 0px;
      left: 0;
      right: 0;
      z-index: 2; }
    .principle {
      padding-top: 40px;
      padding-bottom: 40px;
      @include hover {
        color: $white;
        .underlined {
          @include before {
            transform: translate(0, 0); } }
        .principle__arrow {
          color: $white;
          .icon {
            transform: translate(15px, 0); } }
        .principle__text {
          color: $white; }
        .principle__nmb {
          color: $white; }
        .principle__bg {
          opacity: 1;
          visibility: visible; } }

      @include md {
        padding-top: 30px;
        padding-bottom: 30px; }
      @include sm {
        padding-top: 20px;
        padding-bottom: 20px; }
      @include xs {
        padding-top: 15px;
        padding-bottom: 15px; }
      &__wrap {
        width: 50%;
        border-right: 1px dashed $c-border;
        border-bottom: 1px dashed $c-border;
        margin-bottom: 0;
        @include xss {
          width: 100%;
          border-right-color: transparent; }
        &:nth-child(even) {
          border-right-color: transparent; } }
      &__text {
        margin-bottom: 25px;
        @include md {
          margin-bottom: 15px; }
        @include sm {
          margin-bottom: 10px; } } } }

  &--two-col-simple {
    .principle {
      &__wrap {
        width: 50%; } } } }

.principle {
  $this: &;
  padding-left: 10%;
  padding-right: 10px;
  display: block;
  opacity: 0;
  @include sm {
    padding-left: 0; }
  &__wrap {
    width: 33.33333%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 50px;
    position: relative;
    z-index: 1;
    &:nth-child(odd) {
      #{$this}__bg {
        left: auto;
        right: -15px; } }
    &:nth-child(even) {}

    @include xl {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px;
      width: 50%; }
    @include xs {
      margin-bottom: 10px; }
    @include xss {
      width: 100%; } }
  &__title {
    margin-bottom: 20px;
    display: block;
    @include tr(color .3s);
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  &__nmb {
    margin-bottom: 20px;
    display: block;
    @include tr(color .3s);
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  &__text {
    display: block;
    @include tr(color .3s);
    @include xl {
      br {
        display: none; } } }
  &__arrow {
    color: $gray-light;
    @include tr(color .3s);
    .icon {
      font-size: 8px;
      @include tr(transform .3s); } }
  &__inner {
    display: block;
    position: relative;
    z-index: 1; }
  &__bg {
    position: absolute;
    @include cover;
    z-index: 0;
    top: 0;
    bottom: 0;
    display: block;
    left: -15px;
    width: 50vw;
    opacity: 0;
    visibility: hidden;
    @include tr(opacity .5s, visibility .5s); } }
