.menu {
  $this: &;
  position: fixed;
  z-index: 20;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: $white;
  visibility: hidden;
  opacity: 0;
  color: $white;

  @include flex();
  transform: translate(-100%, 0);
  @include ready {
    @include tr(opacity .5s, visibility .5s, transform .5s); }
  &.is-active {
    visibility: visible;
    opacity: 1;
    transform: translate(0, 0);
    #{$this}__content {
      transform: translate(0, 0);
      opacity: 1; } }
  &__word {
    bottom: 60px;
    left: percentage(470/1920);
    @include xl {
      bottom: 50px; }
    @include lg {
      bottom: 50px; }
    @include md {
      bottom: 40px; }
    @include sm {
      bottom: 25px; }
    @include xs {
      bottom: 15px; } }
  &__top {
    @include flex(flex-start, center);
    height: $header-height;
    margin-bottom: 15px;
    @include sm {
      height: $header-height-sm; } }
  &__inner {
    @include flex(flex-start, normal, column);
    height: 100%; }
  &__content {
    flex-grow: 1;
    background-color: $black;
    transform: translate(-100%, 0);
    position: relative;
    overflow: auto;
    opacity: 0;
    @include scrollbar;
    @include ready {
      @include tr(transform .8s, opacity .8s); }
    > .container {
      height: 100%; } }
  &__logo {
    padding-right: 25px;
    @include xs {
      padding-right: 10px; } }
  &__phone {
    padding-left: 25px;
    position: relative;
    @include xs {
      padding-left: 10px; }
    &::before, {
      content: '';
      position: absolute;
      background-color: $gray;
      width: 1px;
      height: 38px;
      top: 50%;
      transform: translate(0, -50%);
      left: 0; } }
  &__aside {
    // background-color: $white
    width: 110px;
    @include flex(center, center, column);
    position: relative; }
  &__close {
    color: $gray;
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 1;
    @include tr(color .3s);
    @include hover {
      color: $black; }
    @include lg {
      top: 40px; }
    @include md {
      top: 30px; }
    @include sm {
      top: 20px; } }
  &__main {
    @include flex(center, normal, column);
    flex-grow: 1;
    padding-bottom: $header-height;
    @include sm {
      padding-bottom: $header-height-sm; } }
  &__main-in {
    @include flex(flex-start, normal, row, wrap); }
  &__nav {
    @include sm {
      margin-bottom: 25px; } }
  &__left,
  &__center,
  &__right {
    width: 33.33333%; }
  &__left {
    @include sm {
      width: 50%;
      padding-right: 5px; } }
  &__center {
    @include flex(center, normal);
    @include sm {
      width: 50%;
      justify-content: flex-start;
      padding-left: 5px; } }
  &__right {
    @include flex(flex-end, normal);
    @include sm {
      width: 100%;
      justify-content: flex-start; } }
  &__title {
    text-transform: uppercase;
    @include css-lock(16, 24, 320, 1499, font-size);
    @include bold;
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; } }
  &__contacts {
    max-width: 335px;
    // overflow: hidden
    @include sm {
      max-width: none; } } }


