.features {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  @include sm {
    margin-left: -5px;
    margin-right: -5px; }
  &--three-col {
    .feature {
      &__wrap {
        width: 33.33333%; } } } }

.feature {
  color: $gray-light;
  position: relative;
  margin-bottom: 50px;
  opacity: 0;
  @include xl {
    margin-bottom: 40px; }
  @include lg {
    margin-bottom: 30px; }
  @include md {
    margin-bottom: 20px; }
  @include sm {
    margin-bottom: 10px; }
  @include before {
    width: 65px;
    height: 65px;
    position: absolute;
    z-index: 0;
    @include image('black-pattern.png');
    background-repeat: repeat;
    background-size: auto;
    left: -30px;
    top: 25px; }
  &__inner {
    position: relative;
    z-index: 1; }
  &__wrap {
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;
    @include sm {
      padding-left: 5px;
      padding-right: 5px; }
    @include xss {
      width: 100%; } }
  &__icon {
    margin-bottom: 10px;
    background-color: $white;
    @include md {
      margin-bottom: 5px; }
    .icon {
      font-size: 50px; } }
  &__title {
    margin-bottom: 15px;
    color: $black;
    background-color: $white;
    @include md {
      margin-bottom: 10px; } } }
