.pi-hero {
  position: relative;
  background-color: $white;
  padding-bottom: 25%;
  color: $white;
  @include css-lock(30, 150, 320, 1499, margin-top);
  > .container {
    position: relative;
    z-index: 1; }
  @include lg {
    padding-bottom: 20%; }
  @include sm {
    background-color: $black;
    padding-bottom: 10%; }
  &__bg {
    position: absolute;
    z-index: 0;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    @include sm {
      display: none; } }
  &__content {
    @include flex;
    @include sm {
      display: block;
      @include clr; } }
  &__content-text {
    @include sm {
 } }      // padding-left: 15px
  &__logo {
    width: percentage(3/12);
    margin-left: percentage(1/12);
    @include lg {
      margin-left: 0; }
    @include sm {
      float: left;
      margin-right: 15px;
      margin-bottom: 15px; } }
  &__title-block {
    margin-bottom: 55px;
    @include xl {
      margin-bottom: 45px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__descr {
    margin-left: 28%;
    @include lg {
      margin-left: 15%; }
    @include sm {
      margin-left: 0; } }
  &__descr-title {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } }
  &__descr-text {
    max-width: 630px; } }
