.form {
  $this: &;
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px; }
  &__field {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    @include notlast {
      margin-bottom: 30px;
      @include lg {
        margin-bottom: 20px; }
      @include md {
        margin-bottom: 15px; } } }
  &__field-50 {
    width: 50%;
    @include xs {
      width: 100%; } }
  &__checkbox {
    text-align: center;
    line-height: 1.2;
    text-align: center; }
  &__btn {
    margin-bottom: 20px; }


  &--center {
    #{$this}__inner {
        justify-content: center; } }
  &--sm {
    #{$this}__field {
      @include notlast {
        margin-bottom: 15px;
        @include sm {
          margin-bottom: 10px; } } } } }
