.faq {
  @include clr;
  margin-left: -15px;
  margin-right: -15px;
  &--full {
    margin-left: 0;
    margin-right: 0;
    .question {
      &__wrap {
        float: none !important;
        width: 100%; } } } }

.question {
  $this: &;
  opacity: 0;
  text-align: left;
  &__wrap {
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;
    max-width: 570px;
    @include sm {
      width: 100%;
      float: none !important;
      max-width: none;
      width: auto; }
    &:nth-child(odd) {
      float: left;
      clear: left; }
    &:nth-child(even) {
      float: right;
      clear: right; }
    &:last-child {
      .question__title {
        border-bottom: 1px solid  $c-border; } }
    &:first-child {} }
  &__title {
    position: relative;
    display: block;
    padding: 20px 0 15px;
    border-top: 1px solid  $c-border;
    border-bottom: 1px solid  $c-border;
    padding-right: 60px;
    @include bold;
    @include hover {
      .underlined {
        @include before {
          transform: translate(0, 0); } } }
    @include sm {
      padding: 15px 0 10px;
      padding-right: 50px; }
    &.is-active {
      border-bottom-color: transparent;
      #{$this}__icon {
        @include after {
          display: none; } } } }
  &__icon {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    border-radius: 50%;
    background-color: $gray-lighten;
    width: 38px;
    height: 38px;
    @include sm {
      width: 30px;
      height: 30px; }
    &::before,
    &::after {
      content: '';
      background-color: $gray-light;
      border-radius: 3px;
      @include vcenter; }
    &::before {
      width: 11px;
      height: 3px; }
    &::after {
      height: 11px;
      width: 3px; } }


  &__text {
    display: none;
    padding-bottom: 25px;
    padding-right: 60px;
    border-bottom: 1px solid  $c-border;
    color: $gray-light;
    @include sm {
      padding-bottom: 15px;
      padding-right: 40px; } } }


