.gallery {
  $this: &;
  width: percentage(10/12);
  padding-left: 5%;
  padding-bottom: 5%;
  position: relative;
  color: $white;
  @include lg {
    width: percentage(11/12); }
  @include md {
    width: 100%; }
  @include xs {
    padding-left: 0;
    padding-right: 0;
    @include before {
      left: -15px; } }
  @include before {
    position: absolute;
    width: percentage(4/12);
    background-color: $gray-lighten;
    bottom: 0;
    left: 0;
    top: 5%;
    z-index: 0; }
  &.is-visible {
      #{$this}__img {
        opacity: 1; } }
  &__img-wrap {
    position: relative;
    z-index: 1; }
  &__square {
    @include coverdiv;
    z-index: 2;
    pointer-events: none;
    overflow: hidden;
    @include before {
      position: absolute;
      background-color: $yellow;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
      transform: translate(-101%, 0); }
    &.is-animate {
      @include before {
        transform: translate(0, 0);
        @include tr(transform .3s); } }
    &.is-finishing-animate {
      @include before {
        @include tr(transform .3s);
        transform: translate(101%, 0); } } }
  &__inner {
    position: relative;
    padding-top: 40%;
    @include sm {
      padding-top: 50%; } }
  &__img-wrap {
    @include coverdiv; }

  &__img {
    @include cover;
    @include coverdiv;
    background-color: $gray;
    display: none;
    opacity: 0;
    &.is-active {
      display: block; } }
  &__caption {
    position: absolute;
    bottom: 30px;
    left: 30px;
    @include lg {
      bottom: 20px;
      left: 20px; }
    @include md {
      bottom: 15px;
      left: 15px; }
    @include sm {
      bottom: 10px;
      left: 10px; } }
  &__subttl {
    color: $gray-light; }
  &__btns {
    position: absolute;
    z-index: 3;
    bottom: -30px;
    @include css-lock(10, 60, 320, 1499, right);
    @include flex(); }
  &__prev {
    margin-right: 30px;
    @include xl {
      margin-right: 20px; }
    @include lg {
      margin-right: 15px; }
    @include md {
      margin-right: 10px; }
    @include sm {
      margin-right: 5px; } }
  &__prev,
  &__next {
    color: $white; } }
