.pagination {
  text-align: center;
  @include medium;
  color: $gray-light;
  li {
    display: inline-block;
    margin-left: 8px;
    margin-right: 8px;
    @include xs {
      margin-left: 0;
      margin-right: 0; }
    .icon {
      font-size: 8px;
      color: $black;
      @include xs {
        font-size: 5px; }
      @include xss {
        font-size: 3px; } }
    &:first-child,
    &:last-child {
      a {
        padding: 0; } }
    &:first-child {
      .icon {
        @include rotate(180); }
      @include hover {
        a {
          transform: translate(-15px, 0); } } }
    &:last-child {
      @include hover {
        a {
          transform: translate(15px, 0); } } } }

  .is-active {
    color: $black; }

  a {
    display: block;
    padding: 10px;
    @include tr(color .3s, transform .3s);
    @include hover {
      color: $black; }
    @include xs {
      padding: 5px; } }
  .is-inactive {
    pointer-events: none;
    @include xs {
      padding: 0; } } }
