.blog {
  &__articles {}
  &__article {
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 25px; }
    @include xs {
      margin-bottom: 15px; } } }

.article {
  $this: &;
  @include flex(flex-start, normal, row, wrap);
  @include tr(box-shadow .3s);
  @include hover {
    .article-img__img {
      animation-play-state: running; }
    .link-arrow {
      .icon {
        transform: translate(15px, 0); } }
    #{$this}__title {
      color: $purple-light; } }
  &__content {
    width: 50%;
    @include flex(center, normal, column);
    padding-right: 45px;
    opacity: 0;
    @include lg {
      padding-right: 15px; }
    @include sm {
      width: 100%;
      display: block;
      padding-right: 0;
      margin-bottom: 15px; } }
  &__img {
    width: 50%;
    display: block;
    padding-left: 15px;
    overflow: hidden;
    @include sm {
      width: 100%;
      padding-left: 0; } }
  &__title {
    display: block;
    margin-bottom: 20px;
    @include tr(color .3s);
    strong {
      display: block; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    display: block;
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__btn {
    display: block;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__meta {
    display: block;
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } }
  &__user {
    display: block; }

  &--sm {
    display: block;
    padding-left: 0;
    padding-right: 0;
    .article-img {
      @include md {
        padding-left: 5%;
        padding-bottom: 5%; }
      &__img {
        @include md {
          padding-top: 50%; }
        &.is-visible {
         animation-duration: 5s; } } }
    #{$this}__img {
      width: 100%;
      padding: 0;
      margin-bottom: 20px;
      @include sm {
        margin-bottom: 10px; } }
    #{$this}__title {}
    #{$this}__content {
      width: 100%;
      padding: 0; } } }

.meta {
  @include flex(flex-start, center);
  color: $gray-light;
  font-size: 12px;
  &__item {
    @include flex(flex-start, center);
    @include notlast {
      margin-right: 30px;
      @include lg {
        margin-right: 20px; }
      @include md {
        margin-right: 10px; } } }
  &__icon {
    font-size: 0;
    display: block;
    margin-right: 5px;
    .icon {
      font-size: 16px; } } }

.user {
  @include flex(flex-start, center);
  &__img {
    @include cover;
    display: block;
    width: 60px;
    height: 60px;
    flex: 0 0 60px;
    @include md {
      width: 40px;
      height: 40px;
      flex: 0 0 40px; } }
  &__text {
    display: block;
    padding-left: 10px;
    color: $gray-light;
    font-size: 10px;
    line-height: 1.4;
    @include reg; } }

.article-img {
  display: block;
  padding-left: 8%;
  padding-bottom: 8%;
  position: relative;
  @include before {
    @include coverdiv;
    z-index: 0;
    top: 15px;
    left: 0;
    width: 100%;
    background-color: $yellow;
    transform: translate(8%, -8%);
    opacity: 0;
    @include tr(opacity .3s, transform .3s); }
  @include sm {
    padding-left: 4%;
    padding-bottom: 4%; }
  &.is-visible {
    @include before {
      opacity: 1;
      transform: translate(0, 0); } }
  &__inner {
    display: block; }
  &__img {
    @include cover;
    display: block;
    background-color: $gray;
    position: relative;
    z-index: 1;
    padding-top: 85%;
    opacity: 0;
    @include sm {
      padding-top: 50%; }
    &.is-visible {
      animation: scale 10s infinite alternate;
      animation-play-state: paused; } } }

.blog-article {
  article {
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; }
    @include xs {
      margin-bottom: 15px; } }
  img {
    width: 100%;
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0; }
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  h2 {
    @include css-lock(18, 24, 320, 1499, font-size);
    line-height: 1.2;
    @include medium;
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  ol {
    list-style-type: decimal;
    padding-left: 1em; }
  ul {
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 0; }
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; }
    li {
      &::before {
        content: "\2022";
        // width: 1em
        display: inline-block;
        margin-right: .5em; } } }
  li {
    position: relative;
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0; }
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  p {
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0; }
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  a {
    color: $blue;
    text-decoration: underline; }
  blockquote {
    li {
      margin-bottom: 0; } } }

.blog-aside {
  &__article {
    margin-bottom: 90px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 25px; }
    @include xs {
      margin-bottom: 15px; } } }
